@import "./trig-values-and-symbols/trig-symbols.css";

.values_container {
    display:flex;
    flex-wrap: wrap;
    width: 60%;
    height:200px;
    background: #000;

} 

.values_container > * {
    width: 100%
}

.values_container section:nth-child(1){
    color: #fff;
}

.values_container section:nth-child(2){
    color: #fff;
    background: #000;
}


